<!--suppress ALL -->
<template>
  <div>
    <div id="contact">
        <div class="container">
            <div class="row justify-content-center">
            <div class="col-12 text-center mb-5">
                <p>Ne soyez pas timides, je ne mords pas !</p>
                <div class="contact-title">CONTACTEZ-MOI</div>
            </div>
            <div class="col-12 col-lg-9 align-self-center">
                <div class="row justify-content-center">
                    <div class="col-12">
                        <div class="position-relative mb-4">
                            <input type="text" class="subjectCheck check" v-model="form.subject" @change="Changed"/>
                            <label><img src="../assets/images/icon/pen.svg" alt="">Sujet</label>
                        </div>
                    </div>
                    <div class="col-12">
                        <div class="position-relative mb-4">
                            <textarea class="messageCheck check" v-model="form.message" @change="Changed"/>
                            <label><img src="../assets/images/icon/text.svg" alt="">Message</label>
                        </div>
                    </div>
                    <div class="col-12 col-sm-5 col-lg-4" v-if="this.valid">
                        <button class="bt-hover" v-on:click="PostData">CONTACTEZ-MOI</button>
                    </div>
                    <div class="col-12 col-sm-5 col-lg-4" v-if="!this.valid">
                        <p v-html="envoie">{{ envoie }}</p>
                    </div>
                    <div class="col-12">
                        <p class="text-center text-red" v-html="errors">{{ errors }}</p>
                    </div>
                </div>
            </div>

        </div>
        </div>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {
                form: {
                    name: '',
                    firstname: '',
                    email: '',
                    subject: '',
                    message: ''
                },
                envoie: 'Message envoyé',
                errors: '',
                valid: true
            };
        },
        methods: {
            regExp: function(texte){
                let regex = /^[a-zA-Z0-9áàâäãåçéèêëíìîïñóòôöõúùûüýÿæœÁÀÂÄÃÅÇÉÈÊËÍÌÎÏÑÓÒÔÖÕÚÙÛÜÝŸÆŒ()&/!?:@"'$*€=+-._-\s]/g;
                let corresp = regex.exec(texte);

                if(corresp === null) return true;
                return false;
            },
            regEmailExp: function(texte){
                let regex = /^[\w.-]+@[\w.-]+\.[a-zA-Z]{2,6}/g;
                let corresp = regex.exec(texte);

                if(corresp === null) return true;
                return false;
            },
            PostData: function () {
                this.errors = '';
                this.removeClass('.check', 'errorCheck');

                window.open('mailto:contact.mikaeldavid@gmail.com?subject='+this.form.subject.trim()+'&body='+this.form.message.trim()+'');
            },
            addClass: function (selector, myClass) {

                // get all elements that match our selector
                let elements = document.querySelectorAll(selector);

                // add class to all chosen elements
                for (let i=0; i<elements.length; i++) {
                    elements[i].classList.add(myClass);
                }
            },
            removeClass: function (selector, myClass) {
                
                // get all elements that match our selector
                let elements = document.querySelectorAll(selector);

                // remove class from all chosen elements
                for (let i=0; i<elements.length; i++) {
                    elements[i].classList.remove(myClass);
                }
            },
            Changed: function ({ target }) {
                if (target.value === '') {
                    target.classList.remove("doneLabel");
                } else {
                    target.classList.add("doneLabel");
                }

            },
        }
    }
</script>

<style lang="scss" scoped>

    .errorCheck{
        border-bottom-color: red;
    }

    .text-red{
        color: red;
    }

    .lieu-title{
        font-size: 26px;
        margin-bottom: 1rem;
    }

  #contact{
    min-height: 100vh;
    padding-top: 100px;
  }

    .contact-title{
        font-size: 42px;
        font-weight: bold;
    }

    .googleMaps{
        color: white;
        font-size: 16px;
        font-weight: bold;

        &:hover{
            text-decoration: none;
        }
    }

  /* form */
  input, textarea{
      font-size: 16px;
      padding: 10px 10px 10px 24px;
      display: block;
      color: white;
      background-color: #131314;
      width: 100%;
      border: 1px solid #757575;
      border-radius: 0;
  }

  textarea{
      min-height: 130px;
      &:focus{
          outline: 0px;
      }
  }

  input:focus {
      outline: none;
  }

  /* label */
  label{
      color: #999;
      font-size: 16px;
      font-weight: normal;
      position: absolute;
      pointer-events: none;
      left: 5px;
      top: 10px;
      background: #131314;
      padding: 0 10px;
      transition: all 0.2s ease;

      img{
          margin-right: 10px;
          transition: all 0.2s ease;
      }
  }

  /* active */

  input:focus ~ label, input.used ~ label,
  textarea:focus ~ label, textarea.used ~ label,
  .doneLabel ~ label {
      top: -12px;
      transform: scale(.75); left: 4px;

      img{
          opacity: 0;
          width: 0;
          margin-right: 0;
      }
  }


  /* Animations */

  @keyframes inputHighlighter {
      from { background: #4a89dc; }
      to 	{ width: 0; background: transparent; }
  }

  /* Button */
  .bt-hover{
      display: inline-block;
      position: relative;
      width: 100%;
      color: white;
      background: none;
      padding: 12px 24px;
      margin: .3em 0 1em 0;
      letter-spacing: 1px;
      font-weight: 500;
      font-size: 16px;
      outline: none;
      border: 1px solid white;
      transition: all 0.3s;

      &:hover{
          background: white;
          color: #131314;
      }

      &:focus { outline: 0; }
  }

/* transition button */
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>
